import { Component, OnInit } from '@angular/core';
import { Router } from "@angular/router";
@Component({
  selector: 'app-afterlogin',
  templateUrl: './afterlogin.component.html',
  styleUrls: ['./afterlogin.component.css']
})
export class AfterloginComponent implements OnInit {

  constructor(private router: Router) { }
  todoText;
  todoadd;
  regtodoText = "";
  regCheck=true;
  currentEditCounter=-1;

  username = "";
  regUserNameSmall = "";
  email = "";
  regEmailSmall = "";
  regValidEmail = "";

  ngOnInit() {

  }

  logout(){
    alert("You are now Logging out!");
    this.router.navigate(['/loginview']);
  }

  validateRegister(): boolean {
    this.regUserNameSmall = "";
    this.regCheck=true;
    if (this.username.length != 10 ) {
      this.regCheck = false;
      this.regUserNameSmall = "Please enter 10 digit phone number";
    }


    return this.regCheck;
  }


  register() {

    if (this.validateRegister()) {
      alert("Your list has been sent");
      this.username="";
    }


  }

  gototodoapp() {

      this.router.navigate(['/todoApp']);

  }


  gotosendlist() {

      this.router.navigate(['/afterlogin']);

  }

  gotohelp() {

      this.router.navigate(['/helppage']);

  }


  validateemail(): boolean {

    this.regValidEmail = "";
    this.regCheck=true;
  var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

  if (!re.test(this.email)) {
    this.regCheck = false;
    this.regValidEmail = "Please enter a valid email address";
  }
  return this.regCheck;
  }




  registeremail() {

    if (this.validateemail()) {
      alert("Your list has been sent");
      this.email="";
    }


  }







  }



